import React from 'react';
import PropTypes from 'prop-types';

import styles from './Spinner.module.scss';

const Spinner = props => {
  return (
    <div className={styles.Spinner}>
       {props.contentType}.   ...
    </div>
  );
};

Spinner.propTypes = {
  contentType: PropTypes.string.isRequired
};

export default Spinner;