import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/styles';

import SelectableButton from '../UI/SelectableButton';

const styleSheet = (theme) => ({
  BtnItem: {
    '&:not(:last-child)': {
      paddingRight: theme.spacing(1),
    },
  },
});

const ApptProgress = ({
  classes,
  stageChangeHandler,
  activeStage: { type, next, prev },
}) => {
  let btn1Name = '1.  ',
    btn2Name = '2.    ',
    btn3Name = '3.  ';
  let btn1Selected = true,
    btn2Selected = false,
    btn3Selected = false;
  let btn1Disabled = false,
    btn2Disabled = true,
    btn3Disabled = true;
  if (type === 'specialities' || type === 'doctors') {
    btn1Disabled = true;
    btn2Disabled = !next;
  }
  if (type === 'schedule' || type === 'confirm' || type === 'confirmed') {
    btn1Selected = false;
    btn1Name = '1.    ';
    if (type === 'schedule') {
      btn2Disabled = true;
      btn3Disabled = !next;
      btn2Selected = true;
    }
    if (type === 'confirm') {
      btn2Disabled = false;
      btn1Selected = false;
      btn3Selected = true;
      btn2Name = '2.      ';
    }
    if (type === 'confirmed') {
      btn1Name = '1.  ';
      btn1Selected = false;
      btn3Selected = true;
      btn3Name = '3.  ';
    }
  }
  return (
    <Fragment>
      <div className={classes.BtnItem}>
        <SelectableButton
          color='primary'
          variant='outlined'
          onClick={(e) => stageChangeHandler(e, 'specialities')}
          selected={btn1Selected}
          disabled={btn1Disabled}
        >
          {btn1Name}
        </SelectableButton>
      </div>
      <div className={classes.BtnItem}>
        <SelectableButton
          color='primary'
          variant='outlined'
          onClick={(e) => stageChangeHandler(e, 'schedule')}
          selected={btn2Selected}
          disabled={btn2Disabled}
        >
          {btn2Name}
        </SelectableButton>
      </div>
      <div className={classes.BtnItem}>
        <SelectableButton
          color='primary'
          variant='outlined'
          onClick={(e) => stageChangeHandler(e, 'confirm')}
          selected={btn3Selected}
          disabled={btn3Disabled}
        >
          {btn3Name}
        </SelectableButton>
      </div>
    </Fragment>
  );
};

ApptProgress.propTypes = {
  activeStage: PropTypes.object.isRequired,
  stageChangeHandler: PropTypes.func.isRequired,
};

export default withStyles(styleSheet)(ApptProgress);
